import React, {useEffect, useState} from "react";
import MultiGraph from "graphology";
import circlepack from "graphology-layout/circlepack";
import {
    ControlsContainer,
    FullScreenControl,
    SearchControl,
    SigmaContainer,
    useLoadGraph,
    ZoomControl
} from "@react-sigma/core";
import "@react-sigma/core/lib/react-sigma.min.css";

const max_graph_size = 10000
const base_url = 'https://misinfostorage.blob.core.windows.net/$web/infertility_processed'

let loaded_nodes = 0

async function expandFollowers(path: String, files: Set<string>, graph: MultiGraph, positions: { [key: string]: any; }) {
    for (const nodeId of graph.nodes()) {
        let username = graph.getNodeAttribute(nodeId, "username");
        if (!files.has(`${username}.json`)) {
            continue;
        }
        await fetch(`${path}${username}.json`).then((response) => {
            response.json().then((followers) => {
                for (const node of followers.users) {
                    try {
                        const newGraphNode = {
                            id: String(node.id),
                            created_at: Date.parse(node.created_at),
                            username: node.screen_name,
                            x: positions[String(node.id)].x,
                            y: positions[String(node.id)].y,
                            size: 2,
                            label: node.id + " @" + node.username,
                            color: "#4D79A7"
                        };
                        if (!graph.hasNode(String(node.id))) {
                            graph.addNode(String(node.id), newGraphNode);
                            graph.addEdge(nodeId, String(node.id), {
                                type: "arrow",
                                label: "follows",
                                size: 2,
                                created_on: 0,
                                edgeType: "social",
                            });
                        }
                        loaded_nodes = graph.size
                    } catch (e) {
                        console.log(`${e}|${node.id}`)
                    }
                }
            }).catch((e) => {
                console.log(`${e}|${path}|${username}`)
            });
        }).catch((e) => {
            console.log(`${e}|${path}|${username}`)
        });
        console.log(graph.size)
        if (graph.size > max_graph_size) {
            return;
        }
    }
}

async function loadData(graph: MultiGraph) {
    const [nodes_response, positions_response, files_response] = await Promise.all([
        fetch(`${base_url}/graph.json`),
        fetch(`${base_url}/positions.json`),
        fetch(`${base_url}/file_list.json`)
    ]);
    let nodes = await nodes_response.json();
    let positions = await positions_response.json()
    let files_lists = await files_response.json()
    nodes.nodes.map(function (node: any) {
        const newGraphNode = {
            id: node.id,
            created_at: Date.parse(node.created_at),
            username: node.username,
            x: positions[node.id].x,
            y: positions[node.id].y,
            size: 2,
            label: node.id + " @" + node.username,
            color: "red",
            became_misinformative_at: Date.parse("1970")
        };
        if (!graph.hasNode(node.id)) {
            graph.addNode(node.id, newGraphNode);
        }
    });

    nodes.links.map(function (link: any) {
        graph.addEdge(link.tweeter_id, link.retweeter_id, {
            type: "arrow",
            label: "works with",
            size: 2,
            created_at: Date.parse(link.created_at),
            edgeType: "misinformation",
            zIndex: 100,
        });

        // graph.setNodeAttribute(link.tweeter_id, "color", "red")
        // graph.setNodeAttribute(link.retweeter_id, "color", "red")

    });
    const misinformationTimestamps = graph
        .edges()
        .map((n) => ({
            id: n,
            created_at: graph.getEdgeAttribute(n, "created_at") as Number,
            edgeType: graph.getEdgeAttribute(n, "edgeType") as String,
        }))
        .filter(({created_at, edgeType}) => edgeType === "misinformation")
        .map((x) => x.created_at);

    await expandFollowers(`${base_url}/non_base_graph/user_followers/`, new Set(files_lists.followers), graph, positions)
    await expandFollowers(`${base_url}/non_base_graph/user_following/`, new Set(files_lists.following), graph, positions)
}

class Loading extends React.Component<{}, { loaded_nodes: number }> {
    private timerID!: NodeJS.Timer;

    constructor(props: any) {
        super(props);
        this.state = {loaded_nodes: 0};
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            100
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    tick() {
        this.setState({
            loaded_nodes: loaded_nodes
        });
        if (loaded_nodes > max_graph_size){
            clearInterval(this.timerID);
        }
    }


    render() {
        return (
            <div>
                <h2>Loading {max_graph_size} nodes. please wait... Loaded: {this.state.loaded_nodes}.</h2>
            </div>
        );
    }
}

export const LoadGraph = () => {
    const loadGraph = useLoadGraph();
    useEffect(() => {
        const graph = new MultiGraph();
        circlepack.assign(graph);
        loadData(graph).then(() => {
            loadGraph(graph)
        });
    }, [loadGraph]);
    return null;
};


export const DisplayGraph = () => {
    return (
        <div>
            <Loading/>
            <SigmaContainer style={{height: "100vh"}}>
                <LoadGraph/>
            </SigmaContainer>
        </div>

    );
};

export default DisplayGraph;